@import "../../../node_modules/open-color/open-color.css";
@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;600;700&family=Karla:wght@400;600;700&display=swap");

:root {
  --body: "Karla", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
  --code: "Inconsolata", monospace;
  --chart-color: var(--oc-gray-8);
  --font-xsmall: 10px;
  --font-small: 14px;
  --font-normal: 16px;
  --font-large: 20px;
  --font-xlarge: 28px;
  letter-spacing: 0.04em;
  --theme-0: var(--oc-indigo-0);
  --theme-1: var(--oc-indigo-1);
  --theme-2: var(--oc-indigo-2);
  --theme-3: var(--oc-indigo-3);
  --theme-4: var(--oc-indigo-4);
  --theme-5: var(--oc-indigo-5);
  --theme-6: var(--oc-indigo-6);
  --theme-7: var(--oc-indigo-7);
  --theme-8: var(--oc-indigo-8);
  --theme-9: var(--oc-indigo-9);
}

body {
  margin: 16px;
  font-family: var(--body);
}

.multiple-charts {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px
}